<!--  -->
<template>
  <div class="body">
    <!--  -->
    <sysAttach-form ref="sysAttachDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="fileName">
        <el-input v-model="queryForm.fileName" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="fileModule">
        <el-input v-model="queryForm.fileModule" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="id" prop="refId">
        <el-input v-model="queryForm.refId" placeholder="id" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="remarks">
        <el-input v-model="queryForm.remarks" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysAttach:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysAttach:delete']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-export type="primary" size="mini" action="/api/sysAttach/export" title="" v-hasPermi="['sysAttach:export']"/>
      </el-col>
      <el-col :span="1.5">
        <el-import type="primary" size="mini" action="/api/sysAttach/import" title="" v-hasPermi="['sysAttach:import']"/>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="fileName" show-overflow-tooltip sortable/>
      <el-table-column label="" prop="fileSize" sortable/>
      <el-table-column label="" prop="fileModule" sortable/>
      <el-table-column label="" prop="fileType" sortable/>
      <el-table-column label="" prop="suffix" sortable/>
      <el-table-column label="" prop="savePath" show-overflow-tooltip sortable/>
      <el-table-column label="id" prop="refId" show-overflow-tooltip sortable/>
      <el-table-column label="" prop="remarks" sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysAttach:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysAttach:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysAttach:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysAttachForm from './attachForm'
  export default {
    components: {sysAttachForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          fileName: "",
          fileSize: "",
          fileModule: "",
          fileType: "",
          suffix: "",
          savePath: "",
          refId: "",
          remarks: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
    },
    computed: {
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doPost('/api/sysAttach/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //
      openDialog(option) {
        this.$refs.sysAttachDialog.open(option);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("");
          } else {
            that.showWarn("");
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(id);
          that.showSuccess("");
        });
      },
      //
      delete(id) {
        this.$request.doGet('/api/sysAttach/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          }
        });
      },
    }
  };
</script>



