<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="user" ref="passwordForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="loginName">
          <el-input v-model="user.loginName" disabled placeholder="" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="oldPassword">
          <el-input v-model="user.oldPassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPassword">
          <el-input v-model="user.newPassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="prePassword">
          <el-input v-model="user.prePassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "passwordForm",
    data() {
      return {
        roleList: [],
        checkRoleList: [""],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        user: {
          loginName:this.$store.getters.userInfo.loginName,
          oldPassword: "",
          newPassword: "",
          prePassword: ""
        },
        //
        rules: {
          oldPassword: [{required: true, message: '', trigger: 'blur'}],
          newPassword: [{required: true, message: '', trigger: 'blur'}],
          prePassword: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    mounted() {
    },
    methods: {
      //
      open(option) {
        this.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.passwordForm.validate((valid) => {
          if (valid) {//
            if(this.user.newPassword!=this.user.prePassword){
              this.showWarn("");
              return
            }
            that.$request.doPost('/api/sysUser/modifyPsd', that.user).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess(res.info);
                that.$emit('reloadList');
              }else{
                this.showWarn(res.info)
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



