<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysAttach" ref="sysAttachForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysAttach.id"/>
        </el-form-item>
        <el-form-item label="" prop="fileName">
          <el-input v-model="sysAttach.fileName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="fileSize">
          <el-input v-model="sysAttach.fileSize" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="fileModule">
          <el-input v-model="sysAttach.fileModule" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="fileType">
          <el-input v-model="sysAttach.fileType" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="suffix">
          <el-input v-model="sysAttach.suffix" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="savePath">
          <el-input v-model="sysAttach.savePath" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="id" prop="refId">
          <el-input v-model="sysAttach.refId" placeholder="id" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input v-model="sysAttach.remarks" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysAttachForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysAttach: {
          fileName: "",
          fileSize: "",
          fileModule: "",
          fileType: "",
          suffix: "",
          savePath: "",
          refId: "",
          remarks: ""
        },
        //
        rules: {
          fileName: [{required: true, message: '', trigger: 'blur'}],
          fileSize: [{required: true, message: '', trigger: 'blur'}],
          fileModule: [{required: true, message: '', trigger: 'blur'}],
          fileType: [{required: true, message: '', trigger: 'blur'}],
          suffix: [{required: true, message: '', trigger: 'blur'}],
          savePath: [{required: true, message: '', trigger: 'blur'}],
          refId: [{required: true, message: 'id', trigger: 'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysAttach(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSysAttach(id) {
        this.$request.doGet("/api/sysAttach/get?id=" + id).then(res => {
          if (res.isOk) {
            this.sysAttach = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.sysAttachForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysAttach/sub', that.sysAttach).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysAttachForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



