<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysConfig" ref="sysConfigForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysConfig.id"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="sysConfig.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="sysConfig.code" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="val">
          <el-input type="textarea" v-model="sysConfig.val" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysConfigForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysConfig: {
          name: "",
          code: "",
          val: ""
        },
        //
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          code: [{required: true, message: '', trigger: 'blur'}],
          val: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysConfig(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSysConfig(id) {
        this.$request.doGet("/api/sysConfig/get?id=" + id).then(res => {
          if (res.isOk) {
            this.sysConfig = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.sysConfigForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysConfig/sub', that.sysConfig).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysConfigForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



