<template>
  <div class="body">
    <el-card class="box-card" style="height: 350px">
      <div slot="header" class="clearfix">
        <span></span>
        <el-button style="float: right;" size="mini" @click="subConfigs" type="primary"></el-button>
      </div>
      <div class="text item">
        <el-form label-width="100px" class="edit-form">
          <el-form-item label="" class="border">
            <el-input v-model="configs.mailSmtp" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" class="border">
            <el-input v-model="configs.mailPort" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" class="border">
            <el-input v-model="configs.mailName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" class="border">
            <el-input v-model="configs.mailPassword" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>
<script>
  export default {
    name: "base-setting",
    data() {
      return {
      };
    },
    props: {
      configs: {
        type: Object,
        default:{}
      }
    },
    methods: {
      subConfigs(){
        this.$parent.$parent.$parent.subConfigs()
      }
    }
  };
</script>



