<!--  -->
<template>
  <div class="body">
    <!--  -->
    <notice-form ref="noticeDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="title">
        <el-input v-model="queryForm.title" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="queryForm.type" placeholder="" size="small" clearable>
          <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="publishTime">
        <el-date-picker v-model="queryForm.publishTime" placeholder="" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-select v-model="queryForm.sex" placeholder="" size="small" clearable>
          <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="isShow">
        <el-select v-model="queryForm.isShow" placeholder="" size="small" clearable>
          <el-option v-for="dict in isShowDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['notice:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['notice:delete']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-export type="primary" size="mini" action="/api/notice/export" title="" v-hasPermi="['notice:export']"/>
      </el-col>
      <el-col :span="1.5">
        <el-import type="primary" size="mini" action="/api/notice/import" title="" v-hasPermi="['notice:import']"/>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="picture" sortable>
        <template slot-scope="scope">
          <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="" prop="title" sortable/>
      <el-table-column label="" prop="type" sortable>
        <template slot-scope="scope">
          <span v-for="dict in typeDict" v-if="dict.dictValue==scope.row.type">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="content" show-overflow-tooltip sortable/>
      <el-table-column label="" prop="sex" sortable>
        <template slot-scope="scope">
          <span v-for="dict in sexDict" v-if="dict.dictValue==scope.row.sex">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="isShow" sortable>
        <template slot-scope="scope">
          <span v-for="dict in isShowDict" v-if="dict.dictValue==scope.row.isShow">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['notice:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['notice:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['notice:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import noticeForm from './noticeForm'
    export default {
      components: {noticeForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            id: "",
            picture: "",
            title: "",
            type: "",
            publishTime: "",
            content: "",
            sex: "",
            isShow: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
      },
      computed: {
        //
        typeDict() {
          return this.$store.getters.getDictArray('notice_type');
        },
        //
        sexDict() {
          return this.$store.getters.getDictArray('sys_sex');
        },
        //
        isShowDict() {
          return this.$store.getters.getDictArray('sys_yes_no');
        }
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doPost('/api/notice/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //
        openDialog(option) {
          this.$refs.noticeDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("");
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.delete(id);
            that.showSuccess("");
          });
        },
        //
        delete(id) {
          this.$request.doGet('/api/notice/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>



