<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="notice" ref="noticeForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="notice.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="notice_picture" v-model="notice.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="notice.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="notice.type" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="publishTime">
          <el-date-picker v-model="notice.publishTime" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" v-model="notice.content" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-select v-model="notice.sex" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isShow">
          <el-select v-model="notice.isShow" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in isShowDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="fileList">
          <sui-file type="notice" :refId="notice.id" v-model="notice.fileList" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "noticeForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        notice: {
          id: "",
          picture: "",
          title: "",
          type: "",
          publishTime: "",
          content: "",
          sex: "",
          isShow: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          title: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'change'}],
          publishTime: [{required: true, message: '', trigger: 'change'}],
          content: [{required: true, message: '', trigger: 'blur'}],
          sex: [{required: true, message: '', trigger: 'change'}],
          isShow: [{required: true, message: '', trigger: 'change'}]
        }
      }
    },
    computed: {
      //
      typeDict() {
        return this.$store.getters.getDictArray('notice_type');
      },
      //
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
      //
      isShowDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      }
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getNotice(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getNotice(id) {
        this.$request.doGet("/api/notice/get?id=" + id).then(res => {
          if (res.isOk) {
            this.notice = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.noticeForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/notice/sub', that.notice).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.noticeForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



