<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysMessage" ref="sysMessageForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysMessage.id"/>
        </el-form-item>
        <el-form-item label="" prop="msgType">
          <el-select v-model="sysMessage.msgType" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in msgTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="id" prop="pid">
          <el-input v-model="sysMessage.pid" placeholder="id" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="id" prop="userId">
          <el-input v-model="sysMessage.userId" placeholder="id" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="id" prop="refId">
          <el-input v-model="sysMessage.refId" placeholder="id" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" v-model="sysMessage.content" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysMessageForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysMessage: {
          msgType: "",
          pid: "",
          userId: "",
          refId: "",
          content: ""
        },
        //
        rules: {
          msgType: [{required: true, message: '', trigger: 'change'}],
          pid: [{required: true, message: 'id', trigger: 'blur'}],
          userId: [{required: true, message: 'id', trigger: 'blur'}],
          refId: [{required: true, message: 'id', trigger: 'blur'}],
          content: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      msgTypeDict() {
        return this.$store.getters.getDictArray('msg_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysMessage(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSysMessage(id) {
        this.$request.doGet("/api/sysMessage/get?id=" + id).then(res => {
          if (res.isOk) {
            this.sysMessage = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.sysMessageForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysMessage/sub', that.sysMessage).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysMessageForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



