<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="loginName">
          <sui-photo type="user" v-model="sysUser.photo" :disabled="dialog.disabled" :url="sysUser.photo">11</sui-photo>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input v-model="sysUser.loginName"  placeholder="" :disabled="true" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="sysUser.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="sysUser.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="sysUser.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import suiPhoto from "../../../components/sui-photo/sui-photo";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          photo: this.$store.getters.userInfo.photo,
          loginName: this.$store.getters.userInfo.loginName,
          userName: this.$store.getters.userInfo.userName,
          userNo: this.$store.getters.userInfo.userNo,
          email: this.$store.getters.userInfo.email,
          phone: this.$store.getters.userInfo.phone,
          officeId: this.$store.getters.userInfo.officeId
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          email: [{type: 'email', message: '', trigger: ['blur', 'change']}],
          phone: [{required: false,validator:this.$validator.phone}],
        }
      }
    },
    mounted() {
    },
    computed:{
      userInfo(){
        return this.$store.getters.userInfo
      },
    },
    methods: {
      //
      open(option) {
        const that = this;
        that.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.sysUserForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysUser/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reload');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



