<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysRole" ref="sysRoleForm" :rules="rules" label-width="100px">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysRole.id"/>
        </el-form-item>
        <el-form-item label="" prop="roleCode">
          <el-input v-model="sysRole.roleCode" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleName">
          <el-input v-model="sysRole.roleName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input type="textarea" v-model="sysRole.remarks" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysRoleForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysRoleForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysRole: {
          roleCode: "",
          roleName: "",
          remarks: ""
        },
        //
        rules: {
          roleCode: [{required: true, message: '', trigger: 'blur'}],
          roleName: [{required: true, message: '', trigger: 'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysRole(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //id
      getSysRole(id) {
        this.$request.doGet("/api/sysRole/get?id=" + id).then(res => {
          if (res.isOk) {
            this.sysRole = res.obj;
          }
        });
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysRole/sub', that.sysRole).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysRoleForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



