<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="sysQuartz" ref="sysQuartzForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="sysQuartz.id"/>
        </el-form-item>
        <el-form-item label="" prop="jobName">
          <el-input v-model="sysQuartz.jobName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="beanName">
          <el-input v-model="sysQuartz.beanName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="params">
          <el-input v-model="sysQuartz.params" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="cronExpression">
          <el-input v-model="sysQuartz.cronExpression" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="jobType">
          <el-select v-model="sysQuartz.jobType" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in jobTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="hasParams">
          <el-select v-model="sysQuartz.hasParams" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in hasParamsDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="methodName">
          <el-input v-model="sysQuartz.methodName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="jobStatus">
          <el-select v-model="sysQuartz.jobStatus" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in jobStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isExec">
          <el-select v-model="sysQuartz.isExec" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in isExecDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isBootExec">
          <el-select v-model="sysQuartz.isBootExec" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in isBootExecDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="lastExecTime">
          <el-date-picker v-model="sysQuartz.lastExecTime" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input type="textarea" v-model="sysQuartz.remarks" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "sysQuartzForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysQuartz: {
          jobName: "",
          beanName: "",
          params: "",
          cronExpression: "",
          jobType: "",
          hasParams: "",
          methodName: "",
          jobStatus: "",
          isExec: "",
          isBootExec: "",
          lastExecTime: "",
          remarks: ""
        },
        //
        rules: {
          jobName: [{required: true, message: '', trigger: 'blur'}],
          beanName: [{required: true, message: '', trigger: 'blur'}],
          params: [{required: true, message: '', trigger: 'blur'}],
          cronExpression: [{required: true, message: '', trigger: 'blur'}],
          jobType: [{required: true, message: '', trigger: 'change'}],
          hasParams: [{required: true, message: '', trigger: 'change'}],
          methodName: [{required: true, message: '', trigger: 'blur'}],
          jobStatus: [{required: true, message: '', trigger: 'change'}],
          isExec: [{required: true, message: '', trigger: 'change'}],
          isBootExec: [{required: true, message: '', trigger: 'change'}],
          lastExecTime: [{required: true, message: '', trigger: 'change'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      jobTypeDict() {
        return this.$store.getters.getDictArray('job_type');
      },
      //
      hasParamsDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
      //
      jobStatusDict() {
        return this.$store.getters.getDictArray('job_status');
      },
      //
      isExecDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
      //
      isBootExecDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysQuartz(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getSysQuartz(id) {
        this.$request.doGet("/api/sysQuartz/get?id=" + id).then(res => {
          if (res.isOk) {
            this.sysQuartz = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.sysQuartzForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysQuartz/sub', that.sysQuartz).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysQuartzForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



