<template>
  <el-card class="box-card" style="height: 350px">
    <div slot="header" class="clearfix">
      <span></span>
      <el-button style="float: right;" size="mini" @click="send" type="primary"></el-button>
    </div>
    <div class="text item">
      <el-form :model="mail" ref="mailForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="" prop="mailUser" class="border">
          <el-input v-model="mail.mailUser" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="mailTitle" class="border">
          <el-input v-model="mail.mailTitle" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="mailContent" class="border">
          <el-input type="textarea" v-model="mail.mailContent" placeholder=""></el-input>
        </el-form-item>
      </el-form>
    </div>
  </el-card>
</template>
<script>
  export default {
    name: "base-setting",
    data() {
      return {
        mail: {
          mailUser: "",
          mailTitle: "",
          mailContent: "",
        },
        //
        rules: {
          mailUser: [{required: true, message: '', trigger: 'blur'},{type: 'email', message: '', trigger: ['blur', 'change']}],
          mailTitle: [{required: true, message: '', trigger: 'blur'}],
          mailContent: [{required: true, message: '', trigger: 'blur'}]
        }
      };
    },
    methods:{
      send(){
        const that = this;
        that.$refs.mailForm.validate((valid) => {
          if (valid) {//
            that.showConfirm("?", function () {
              that.$request.doPost('/api/sys/mail/send', that.mail).then(res => {
                if (res.isOk) {
                  that.showSuccess("");
                };
              });
            })
          }
        });
      }
    }
  };
</script>



